.App {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: stretch;
}

.flexRowContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.defaultUserInfoInput {
  padding: 0.75vh 0.75vw;
  margin: 0.5vh 0.5vw;
}

.generalUseWideButton {
  padding: 0.75vh 0.75vw;
  margin: 0.5vh 0.5vw;
  color: rgba(254, 253, 253, 0.99);
  text-shadow: 0.5px 0.5px 0 rgba(50, 50, 50, 0.95);
  background: rgba(255, 221, 0, 0.99)
}

.generalUseWideButtonInLink {
  padding: 0.5vh 0.5vw;
  margin: 0;
  border: 0.25px solid rgba(82, 82, 82, 0.99);
  background: rgba(250, 250, 250, 0.99);
  color: rgba(82, 82, 82, 0.99);
}

.verticalLine {
  width: 0.5px;
  border-radius: 1px;
  justify-self: stretch;
  align-self: stretch;
  background: black;
}

.signupRedirectionDiv {
  margin: 2vh 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

